import { NavLink, Link } from "react-router-dom";
import { connect } from "react-redux";
import React from 'react'

import { userLogin, userLogout, userSignup } from '../store/actions/user.action.js'
import { LoginSignup } from './login-signup.jsx'
import { Cart } from './cart.jsx'
import login from '../assets/img/login.svg'

class _AppHeader extends React.Component {

   state = {
      isModalOpen: false,
      isCartOpen: false
   }

   onLogin = (credentials) => {
      this.props.userLogin(credentials)
   }

   onSignup = (credentials) => {
      this.props.userSignup(credentials)
   }

   onLogout = () => {
      this.props.userLogout()
   }

   onOpenModal = () => {
      this.setState({ isModalOpen: !this.state.isModalOpen })
   }

   onCartOpen = () => {
      this.setState({ isCartOpen: !this.state.isCartOpen })
   }

   render() {
      const { user } = this.props
      const { isModalOpen, isCartOpen } = this.state
      return (
         <header className="app-header">
            <h1>Toy App</h1>
            <nav>
               <NavLink exact to="/">Home</NavLink> |
               <NavLink to="/toy">Toys</NavLink> |
               <NavLink to="/dashboard">Dashboard</NavLink> |
               <NavLink to="/about">About</NavLink> 
            </nav>

            <div className="right-header">
               <div onClick={this.onCartOpen}></div>
               {isCartOpen && <Cart user={user}/>}

            {user &&
               <section className="user-info">
                  <button className='logout' onClick={this.onLogout}> Logout</button>
               </section>}
            {!user &&
               <section className="user-info">
                  <Link to="/login"><img src={login} alt="" /></Link>
                  {/* <button className='logout' onClick={this.onOpenModal}> Login</button> */}
                  {isModalOpen && <LoginSignup heading={'Just checking'} onLogin={this.onLogin} onSignup={this.onSignup} />}
               </section>}

         </div>

         </header >
      )
   }
}

const mapStateToProps = (storeState) => {
   return {
      user: storeState.userModule.user
   }
}

const mapDispatchToProps = {
   userLogin,
   userLogout,
   userSignup
}

export const AppHeader = connect(
   mapStateToProps,
   mapDispatchToProps

)(_AppHeader)