
import React from 'react'
import Select from 'react-select';
import makeAnimated from 'react-select/animated';

const options = [
   { value: 'On wheels', label: 'On wheels' },
   { value: 'Box game', label: 'Box game' },
   { value: 'Art', label: 'Art' },
   { value: 'Baby', label: 'Baby' },
   { value: 'Doll', label: 'Doll' },
   { value: 'Puzzle', label: 'Puzzle' },
   { value: 'Outdoor', label: 'Outdoor' },
];

const animatedComponents = makeAnimated();

export class ToyFilter extends React.Component {

   state = {
      filterBy: {
         stock: 'All',
         sort: '',
         name: '',
         selectedOption: []
      }
   }

   componentDidMount() {
      const { filterBy } = this.props
      if (!filterBy) return
      this.setState({ filterBy })
   }

   handlerChange = ({ target }) => {
      let { name, value } = target
      if (name === 'stock') {
         if (value === 'false') value = false
         else if (value === 'true') value = true
         else value = ''
      }
      this.setState((prevState) => ({ filterBy: { ...prevState.filterBy, [name]: value } }),
         () => {
            if (name === 'stock' || name === 'sort') this.onSubmit()
         })
   }

   handlerChangeSelect = (labels) => {
      this.setState((prevState) => ({ filterBy: { ...prevState.filterBy, selectedOption: labels } }),
         () => { this.onSubmit() })
   }

   onSubmit = (ev) => {
      !!ev && ev.preventDefault()
      const { onFilterBy } = this.props
      onFilterBy(this.state.filterBy)
   }

   render() {
      const { stock, name, sort, selectedOption } = this.state.filterBy
      return (
         <section>
            <form onSubmit={this.onSubmit} className="toy-filter-form">

               <label htmlFor="stock">Stock</label>
               <select name="stock" id="stock" value={stock} onChange={this.handlerChange}>
                  <option value="">All</option>
                  <option value={true}>In stock</option>
                  <option value={false}>Out of stock</option>
               </select>

               <label htmlFor="name">search toy</label>
               <input type="search" placeholder="Search toy" name="name" id="name" value={name} onChange={this.handlerChange} />

               <Select
                  closeMenuOnSelect={false}
                  components={animatedComponents}
                  onChange={this.handlerChangeSelect}
                  defaultValue={[options[0], options[1]]}
                  isMulti
                  options={options}
                  value={selectedOption}
               />

               <label htmlFor="sort">Sort by</label>
               <select name="sort" id="sort" value={sort} onChange={this.handlerChange}>
                  <option value="Newest" >Date: Newest</option>
                  <option value="Oldest">Date: Oldest</option>
                  <option value="Higher">Price: Higher</option>
                  <option value="Lower">Price: Lower</option>
               </select>
            </form>
         </section>
      )
   }
}