
import React from 'react'

export class ChatApp extends React.Component {

   state = {
      question: '',
      questions: [],
      answers: [],
   }

   handlerChange = ({ target }) => {
      const { value } = target
      this.setState({ question: value })
   }

   onSubmit = (ev) => {
      !!ev && ev.preventDefault()
      const { question, questions, answers } = this.state
      const updateQuestion = [...questions, question]
      this.setState({ questions: updateQuestion, question: '' }, () => {
         setTimeout(() => {
            const updateAnswers = [...answers, 'Support: Sure thing honey']
            this.setState({ answers: updateAnswers })
         }, 2000);
      })
   }

   render() {
      const { question, questions, answers } = this.state
      return (
         <section className="chat-app">

            <div className="chat">
               {questions.map((q, idx) => {
                  return <div key={idx}>
                     <p className='question' >You: {q}</p>
                     <p className='answer' >{answers[idx]}</p>
                  </div>
               })}
            </div>

            <form onSubmit={this.onSubmit}>
               <input type="search" placeholder="Ask question" name="question" value={question} onChange={this.handlerChange} />
               <input type="submit" value="Send" />
            </form>
         </section>
      )
   }
}
