import { Link } from "react-router-dom";
import { connect } from "react-redux";
import React from 'react'

import { removeToy, editToy } from '../store/actions/toy.action.js'
import { toyService } from '../services/toy.service.js'

class _ToyEdit extends React.Component {

    state = {
        toy: {
            inStock: false,
            price: '',
            createdAt: '',
            labels: [],
            name: ''
        }
    }

    componentDidMount() {
        const { toyId } = this.props.match.params
        toyService.getById(toyId)
            .then(toy => {
                this.setState({ toy }, () => {
                    const date = new Date(toy.createdAt).toISOString().replace(/T.*/, '').split('-').join('-');
                    const toyEdit = { ...toy, createdAt: date }
                    this.setState({ toy: toyEdit })
                })
            })
    }

    onRemoveToy = (toyId) => {
        this.props.removeToy(toyId)
        this.props.history.push('/toy/')
    }

    onSubmit = (ev) => {
        !!ev && ev.preventDefault()
        const { toy } = this.state
        const toyEdit = { ...toy, createdAt: Date.parse(toy.createdAt) }
        this.props.editToy(toyEdit)
        this.props.history.push(`/toy/details/${toy._id}`)
    }

    handlerChange = ({ target }) => {
        const { name, value } = target
        if (name === 'labels') {
            const { labels } = this.state.toy
            if (!labels.includes(value)) labels.push(value)
            console.log(labels);
            // this. setState((prevState) => ({ toy: { ...prevState.toy, labels: updateLabels } }))
            // console.log('updateLabels', updateLabels);
        }

        else if (name === 'price') {
            this.setState((prevState) => ({ toy: { ...prevState.toy, [name]: +value } }))
        } else {
            this.setState((prevState) => ({ toy: { ...prevState.toy, [name]: value } }))
        }
    }

    render() {
        const { toy } = this.state
        const { inStock, name, price, createdAt } = this.state.toy

        return (
            <div>
                <main>
                    <h3 className="edit-header" >Edit your toy</h3>
                    <form onSubmit={this.onSubmit} className="toy-edit-form">

                        <label htmlFor="name"><span>Name</span>
                            <input type="text" name="name" id="name" value={name} onChange={this.handlerChange} />
                        </label>

                        <label htmlFor="price"><span>Price</span>
                            <input type="number" min={0} name="price" id="price" value={price} onChange={this.handlerChange} />
                        </label>

                        <label htmlFor="createdAt"><span>Date</span>
                            <input type="date" name="createdAt" id="createdAt" value={createdAt} onChange={this.handlerChange} />
                        </label>

                        <label htmlFor="inStock"><span>Stock</span>
                            <select name="inStock" id="inStock" value={inStock} onChange={this.handlerChange}>
                                <option value={true}>In stock</option>
                                <option value={false}>Out of stock</option>
                            </select>
                        </label>

                        <label htmlFor="labels"><span>Labels</span>
                            <select name="labels" id="labels" onChange={this.handlerChange} className="multi-select-form" multiple>
                                <option value="On wheels">On wheels</option>
                                <option value="Box game">Box game</option>
                                <option value="Art">Art</option>
                                <option value="Baby">Baby</option>
                                <option value="Doll">Doll</option>
                                <option value="Puzzle">Puzzle</option>
                                <option value="Outdoor">Outdoor</option>
                            </select>
                        </label>

                        <button type="submit" value="Save" id="" >Save</button>

                    </form>
                    <div className="edit-btn">
                        <button><Link to={`/toy/details/${toy._id}`}>Back</Link></button>
                        <button onClick={() => { this.onRemoveToy(toy._id) }}>Delete</button>
                    </div>
                </main>
            </div>
        )
    }
}

const mapStateToProps = (storeState) => {
    return {
        toys: storeState.toyModule.toys
    }
}

const mapDispatchToProps = {
    removeToy,
    editToy
}

export const ToyEdit = connect(
    mapStateToProps,
    mapDispatchToProps

)(_ToyEdit)
