
import React from 'react'

export class Cart extends React.Component {

   render() {
      const { cart } = this.props.user
      const total = cart.reduce((prev, curr) => prev.price + curr.price , 0)
      return (
         <section className='cart'>
            Cart:
            <hr />
            <ol>
               {cart.map((item, idx) => <li key={idx}>{item.name} | ${item.price} | {item.qty}</li>)}
            </ol>
            <hr />
            {total}

            Total: 50$
         </section>
      )
   }
}