import { useState, useEffect } from 'react'

import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { showErrorMsg, showSuccessMsg } from '../services/event-bus.service'

import { loadReviews, addReview, removeReview } from '../store/actions/review.action'
import { loadUsers } from '../store/actions/user.action'

function _ToyReview({ reviews, users, loggedInUser, loadReviews, loadUsers, addReview, removeReview, toy }) {

   const [reviewToEdit, setReviewToEdit] = useState({ content: '', aboutToyId: '' })

   useEffect(() => {
      loadReviews()
      loadUsers()
   }, [])

   const handleChange = ev => {
      const { name, value } = ev.target
      setReviewToEdit({ ...reviewToEdit, [name]: value, aboutToyId: toy._id })
   }

   const onAddReview = async ev => {
      ev.preventDefault()
      if (!reviewToEdit.content || !reviewToEdit.aboutToyId) return alert('All fields are required')
      await addReview(reviewToEdit)
      // await loadReviews()
      showSuccessMsg('Review added')
      setReviewToEdit({ content: '', aboutToyId: '' })
   }

   const onRemove = async reviewId => {
      try {
         await removeReview(reviewId)
         showSuccessMsg('Review removed')
      } catch (err) {
         showErrorMsg('Cannot remove')
      }
   }

   // const canRemove = review => (review.byUser._id === loggedInUser?._id || loggedInUser?.isAdmin)
   //const matchToyToReview = (review, toy) => (review.aboutToy._id === toy._id)

   return (
      <div className="review-app">
         <h1>Reviews</h1>
         {reviews &&
         
            <ul className="review-list">

               {console.log('reviews', reviews[reviews.length-1])}
               {/* {console.log('users', users)}
               {console.log('loggedInUser', loggedInUser)} */}
               {/* {console.log('toy', toy._id )}
               {console.log('aboutToy._id', reviews[0].aboutToy._id )} */}

               { reviews.map(review => (
                  review?.aboutToy?._id && review.aboutToy._id === toy._id &&
                  <li key={review._id}> { /*canRemove(review) &&*/ <button onClick={() => onRemove(review._id)}>X</button> }
                     <p>{/* About:<Link to={`/user/${review.aboutToyId._id}`}>{review.aboutToyId.fullname}</Link> */}</p>
                     <h3>_id: {review._id}</h3>
                     {/* <h3>userId: {review.userId}</h3>
                     <h3>toyId: {review.toyId}</h3> */}
                     <h3>content: {review.content}</h3>
                     <p>{/* By:<Link to={`/user/${review.byUser._id}`}>{review.byUser.fullname}</Link> */}</p>
                  </li>
               ))}
            </ul>}

         {users && loggedInUser &&
            <form onSubmit={onAddReview}>
               <textarea name="content" onChange={handleChange} value={reviewToEdit.content} ></textarea>
               <button>Submit</button>
            </form>}
         <hr />
      </div>
   )
}

const mapStateToProps = state => {
   return {
      reviews: state.reviewModule.reviews,
      users: state.userModule.users,
      loggedInUser: state.userModule.user,
      toy: state.toyModule.toy
   }
}
const mapDispatchToProps = {
   loadReviews,
   loadUsers,
   addReview,
   removeReview
}

export const ToyReview = connect(mapStateToProps, mapDispatchToProps)(_ToyReview)