
import React from 'react'
import { Map } from '../cmps/map.jsx'

export class AboutUs extends React.Component {

    state = {
        branch: { lat: 32.0716623, lng: 34.7848943 }
    }

    onChangeBranch = (branch) => {
        if (branch === 'Tel Aviv') {this.setState({ branch: { lat: 32.0716623, lng: 34.7848943 } })}
        if (branch === 'Eilat') {this.setState({ branch: { lat: 29.568518, lng: 34.9590023 } })}
        if (branch === 'Haifa') {this.setState({ branch: { lat: 32.7908821, lng: 34.9630955 } })}
    }

    render() {
        const { branch } = this.state
        return (
            <section className='app-about'>
                <h2>About Us</h2>
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Magni aperiam quo veniam velit dolor reprehenderit,
                    laudantium consequatur neque numquam labore quae. Accusamus libero perferendis ducimus? Alias unde hic quisquam doloremque.
                </p>
                <div className="branches">
                    <button onClick={() => { this.onChangeBranch('Eilat') }} >Eilat</button>
                    <button onClick={() => { this.onChangeBranch('Haifa') }} >Haifa</button>
                    <button onClick={() => { this.onChangeBranch('Tel Aviv') }} >Tel Aviv</button>
                </div>
                <Map branch={branch}/>
            </section>
        )
    }
}