
import { Link } from "react-router-dom";
import React from 'react'

import toy1 from '../assets/img/toys/toy1.png'
import toy2 from '../assets/img/toys/toy2.png'
import toy3 from '../assets/img/toys/toy3.png'
import toy4 from '../assets/img/toys/toy4.png'
import toy5 from '../assets/img/toys/toy5.png'
import toy6 from '../assets/img/toys/toy6.png'
import toy7 from '../assets/img/toys/toy7.png'
import toy8 from '../assets/img/toys/toy8.png'
import toy9 from '../assets/img/toys/toy9.png'
import toy10 from '../assets/img/toys/toy10.png'
import toy11 from '../assets/img/toys/toy11.png'
import toy12 from '../assets/img/toys/toy12.png'
import toy13 from '../assets/img/toys/toy13.png'
import toy14 from '../assets/img/toys/toy14.png'

export function ToyPreview({ toy }) {
    const images = [toy1, toy2, toy3, toy4, toy5, toy6, toy7, toy8, toy9, toy10, toy11, toy12, toy13, toy14]
    return (
        <Link to={`/toy/details/${toy._id}`}>
            <img src={images[toy.img]} alt="" />
            <div className="preview-txt">
                <p >{toy.name} </p> |
                <p >${toy.price} </p>
            </div>
        </Link>
    )
}
